<template>
  <div id="top-block">
    <div class="wrapper">
      <img
        alt="home-bg"
        id="para"
        class="spacer"
        :src="require(`@/assets/${bgImage}`)"
        rel="preload"
      />
      <div class="tinted">
        <router-link to="/" id="logo-link">
          <div class="logo-wrapper">
            <img alt="logo" class="logo" src="@/assets/logo-optyka.png" />
            <span id="logo-name">optyka i optometria</span>
          </div>
        </router-link>
        <ul class="bar-wrapper">
          <li class="bar-item">
            <router-link class="bar-link" to="/">HOME</router-link>
          </li>
          <li class="bar-item">
            <router-link class="bar-link" :to="'badania'">BADANIA</router-link>
          </li>
          <li class="bar-item">
            <router-link class="bar-link" :to="'oferta'">OFERTA</router-link>
          </li>
          <li class="bar-item">
            <router-link class="bar-link" :to="'zespol'">ZESPÓŁ</router-link>
          </li>
        </ul>
        <transition name="slide-fade">
          <div v-if="slide" class="text-block">
            <p class="text-bl">
              {{ this.blText }}
            </p>
            <p class="text-gr">
              {{ this.grText }}
            </p>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import SimpleParallax from 'simple-parallax-js'

export default {
  name: 'Header',
  props: ['bgImage', 'blText', 'grText'],
  data: function() {
    return {
      slide: false,
      enPath: '',
      imgToLoad: '@/assets/' + this.bgImage,
    }
  },
  methods: {
    languageRoute: function() {
      if (this.$route.path.includes('en')) {
        return this.$route.path.substr(3)
      } else {
        return '/en' + this.$route.path
      }
    },
  },
  created() {
    const img = new Image()
    img.src = this.imgToLoad
    img.onload = () => {
      this.slide = true
    }
    this.enPath = this.languageRoute()
  },
  mounted() {
    const image = document.getElementById('para')
    new SimpleParallax(image, {
      scale: 1.3,
      transition: 'cubic-bezier(0,0,0,1)',
      orientation: 'down',
      delay: 1.1,
    })
    this.slide = true
  },
}
</script>

<style scoped>
#header {
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100vw;
}

/* tinted background begin */
.spacer {
  display: block;
  width: 100%;
  max-height: 100vh;
  z-index: -2;
}

.tinted {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(
      161.35deg,
      rgba(160, 160, 160, 0.37) 25.07%,
      rgba(196, 196, 196, 0) 83.97%
    ),
    linear-gradient(
      93.6deg,
      rgba(160, 160, 160, 0.77) 1.87%,
      rgba(196, 196, 196, 0) 46.97%
    );
}
/* tinted background end */

/* white logo link begin */
@media screen and (max-width: 767px) {
  #logo-link {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  .logo-wrapper {
    top: 3%;
    width: 240px;
    height: 18%;
  }

  #logo-name {
    top: 87px;
    font-size: 24px;
  }
}

/* TODO proper positioning */
.logo-wrapper {
  position: absolute;
  top: 6%;
  left: 2%;
  width: 25%;
  height: 6em;
  margin: 0;
  padding: 0;
}

.logo {
  position: absolute;
  width: 65%;
  height: auto;
  left: 12%;
  top: 0;
  margin: 0;
  transform: translateY(-10%);
}

@media screen and (min-width: 1441px) {
  .logo {
    width: 234px;
  }
}

#logo-name {
  position: absolute;
  width: 100%;
  height: auto;
  left: 7%;
  top: 78%;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 1.1em;

  color: #ffffff;
}
/* white logo link end */

/* desktop menu start */
.bar-wrapper {
  position: absolute;
  top: 7.5%;
  right: 3%;
  width: 550px;
  height: 8.2%;
  list-style-type: none;
}
@media screen and (max-width: 767px) {
  .bar-wrapper {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  .bar-wrapper {
    width: 650px;
  }
}

.bar-item {
  float: left;
  position: relative;
  padding: 0;
  width: 20%;
}

.bar-link {
  width: 100%;
  height: auto;
  display: block;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 0.95em;
  text-align: center;
  text-decoration: none;

  color: #ffffff;
  transition: 0.75s;
}

.bar-link:hover {
  color: #34c231;
  text-decoration: none;
}

.bar-link::before {
  content: '';
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -80%;
  height: 1px;
  background: #34c231;
  opacity: 0;
  transition: 0.75s;
}

.bar-link:hover:before {
  opacity: 1;
}
/* desktop menu end */

/* text start */
.slide-fade-enter-active {
  transition: all 2s ease;
}
.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(30px);
  opacity: 0;
}

.text-block {
  position: absolute;
  left: 4%;
  top: 45%;
  max-width: 40%;
  height: auto;
}

.text-bl::before {
  content: '';
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -0.1em;
  height: 1px;
  background: #34c231;
}

.text-bl {
  position: relative;
  margin: 0;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 2.1em;
  line-height: 1.15em;
  color: #000;
}

.text-gr {
  margin: 0;
  padding: 0;
  padding-top: 0.4em;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 1em;
  color: #e6e6e6;
}

@media screen and (max-width: 767px) {
  .text-block {
    top: 30%;
  }

  .text-bl {
    font-size: 2.2em;
  }

  .text-gr {
    font-size: 1.4em;
  }
}

/* text end */
</style>
